/* Skills Styling For Bar */
.skills {
  position: relative;
}

.skills li {
  list-style: none !important;
  margin-bottom: 50px;
  background: #e9e5e2;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#e1ddd9),
    to(#e9e5e2)
  );
  background-image: linear-gradient(top, #e1ddd9, #e9e5e2);
  height: 20px;
  border-radius: 10px;
  -moz-box-shadow: 0 1px 0px #bebbb9 inset, 0 1px 0 #fcfcfc;
  -webkit-box-shadow: 0 1px 0px #bebbb9 inset, 0 1px 0 #fcfcfc;
  box-shadow: 0 1px 0px #bebbb9 inset, 0 1px 0 #fcfcfc;
}

.skills li h3 {
  position: relative;
  top: -20px;
  font-size: 16.5px;
  font-weight: 600;
  color: rgb(0, 0, 0);
}

.bar {
  height: 18px;
  margin: 0px 6px;
  position: absolute;
  left: 0;
  border-radius: 10px;
  -moz-box-shadow: 0 1px 0px #fcfcfc inset, 0 1px 0 #bebbb9;
  -webkit-box-shadow: 0 1px 0px #fcfcfc inset, 0 1px 0 #bebbb9;
  box-shadow: 0 1px 0px #fcfcfc inset, 0 1px 0 #bebbb9;
}

.html {
  width: 90%;
  animation: html 2.7s ease-out;
  -webkit-animation: html 2.7s ease-out;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#fb4a21),
    to(#fb4a21)
  );
}

.css {
  width: 80%;
  animation: css 2.7s ease-out;
  -webkit-animation: css 2.7s ease-out;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#fbff12),
    to(#fbff12)
  );
}

.js {
  width: 75%;
  animation: js 2.7s ease-out;
  -webkit-animation: js 2.7s ease-out;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#00bbf9),
    to(#00bbf9)
  );
}

.py {
  width: 90%;
  animation: py 2.7s ease-out;
  -webkit-animation: py 2.7s ease-out;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#89fc00),
    to(#89fc00)
  );
}

.sql {
  width: 65%;
  animation: sql 2.7s ease-out;
  -webkit-animation: sql 2.7s ease-out;
  background-color: #f674a4;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#ff01fb),
    to(#ff01fb)
  );
}

@keyframes html {
  0% {
    width: 0px;
  }
  100% {
    width: 90%;
  }
}
@keyframes css {
  0% {
    width: 0px;
  }
  100% {
    width: 80%;
  }
}
@keyframes js {
  0% {
    width: 0px;
  }
  100% {
    width: 75%;
  }
}
@keyframes py {
  0% {
    width: 0px;
  }
  100% {
    width: 90%;
  }
}
@keyframes sql {
  0% {
    width: 0px;
  }
  100% {
    width: 65%;
  }
}

.card--1 .card__img,
.card--1 .card__img--hover {
  /* background-image: url('../public/images/img-1.png'); */
  background-color: #f674a4;
}

.card--2 .card__img,
.card--2 .card__img--hover {
  background-color: #89fc00;
}

.card--3 .card__img,
.card--3 .card__img--hover {
  background-color: coral;
}

.card__like {
  width: 20px;
}

.card__img {
  visibility: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card__info-hover {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
}

.card__img--hover {
  transition: 0.2s all ease-out;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  position: absolute;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  top: 0;
}
.card {
  margin-right: 25px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 60%;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}
.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.1, 1.1);
}

.card__info {
  z-index: 2;
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}

.card__category {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
}

.card__title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Roboto Slab", serif;
}

.card__by {
  font-size: 12px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}

.card__author {
  font-weight: 600;
  text-decoration: none;
  color: #ad7d52;
}

.card:hover .card__img--hover {
  height: 100%;
  opacity: 0.3;
}

.card:hover .card__info {
  background-color: transparent;
  position: relative;
}

.card:hover .card__info-hover {
  opacity: 1;
}

